<template>
  <scroll class="wrapper"
          :data="data"
          :pulldown="pulldown"
          @pulldown="loadData">
    <ul class="content">
      <li v-for="(key, value) in data" :key="value">{{key}}</li>
    </ul>
    <div class="loading-wrapper">
      loadMore
    </div>
  </scroll>
</template>

<script>
import scroll from "./scroll";
import BScroll from 'better-scroll'
export default {
  name: 'HelloWorld',
  data () {
    return {
      data: [
        1,2,3,4,5,6,7,8,8,8,7,5,4,3,3,2,456,565
      ],
      pulldown: true
    }
  },
  components: {
    scroll
  },
  created() {
    this.loadData()
  },
  methods: {
    loadData() {
      console.log('loadData');
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



